import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { motion, useAnimation } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

const ArchiveSection = styled.section`
  background-color: var(--dark);
  padding: 6rem 1rem;
  position: relative;
  overflow: hidden;
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at center, rgba(255, 0, 34, 0.05) 0%, rgba(0, 0, 0, 0) 70%);
    pointer-events: none;
  }
`;

const GridContainer = styled(motion.div)`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
  
  @media (min-width: 768px) {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  }
`;

const ArchiveMediaContainer = styled.div`
  position: absolute; /* Changed from relative to fill FileImage */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #000; /* Fallback background */

  iframe,
  img,
  video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: none;
  }
`;

const FileCard = styled(motion.div)`
  background-color: rgba(20, 20, 20, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.1);
  aspect-ratio: 3/4;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease;
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.8) 100%);
    z-index: 1;
    opacity: 0.7;
    transition: opacity 0.3s ease;
  }
  
  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(255, 0, 34, 0.2);
    border-color: rgba(255, 0, 34, 0.3);
    
    &:before {
      opacity: 0.4;
    }
  }
`;

const FileImage = styled.div`
  width: 100%;
  height: 100%;
  position: relative;
  
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.5s ease;
  }
`;

const FileInfo = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 1rem;
  z-index: 2;
  color: var(--white);
`;

const FileName = styled.h3`
  font-size: 1rem;
  margin-bottom: 0.5rem;
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  color: var(--white);
`;

const FileType = styled.div`
  font-size: 0.8rem;
  opacity: 0.7;
  display: flex;
  align-items: center;
  
  &:before {
    content: '';
    display: inline-block;
    width: 8px;
    height: 8px;
    background-color: var(--red);
    border-radius: 50%;
    margin-right: 0.5rem;
  }
`;

const ClassifiedStamp = styled.div`
  position: absolute;
  top: 1rem;
  right: 1rem;
  background-color: var(--red);
  color: var(--white);
  padding: 0.2rem 0.5rem;
  font-size: 0.7rem;
  transform: rotate(15deg);
  z-index: 2;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 1px;
`;

// Modal components
const ModalOverlay = styled(motion.div)`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85);
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  backdrop-filter: blur(10px);
`;

const ModalContent = styled(motion.div)`
  background-color: #0a0a0a;
  border: 1px solid rgba(255, 0, 34, 0.2);
  width: 100%;
  max-width: 900px;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  padding: 2rem;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  
  @media (min-width: 768px) {
    grid-template-columns: 1fr 1fr;
  }
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)' opacity='0.03'/%3E%3C/svg%3E");
    pointer-events: none;
    opacity: 0.05;
    z-index: -1;
  }
`;

const ModalImageContainer = styled.div`
  width: 100%;
  height: 100%;
  min-height: 300px;
  position: relative;
  overflow: hidden;
  
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0.7));
    z-index: 1;
    pointer-events: none;
  }
`;

const ModalInfo = styled.div`
  color: var(--white);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const ModalHeader = styled.div`
  margin-bottom: 1.5rem;
  position: relative;
  padding-bottom: 1rem;
  
  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100px;
    height: 2px;
    background-color: var(--red);
  }
`;

const ModalTitle = styled.h3`
  font-size: 2rem;
  margin-bottom: 0.5rem;
  color: var(--white);
  text-transform: uppercase;
`;

const ModalSubtitle = styled.p`
  color: var(--gray-light);
  font-size: 1rem;
`;

const ModalDescription = styled.div`
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
  
  p {
    margin-bottom: 1rem;
  }
`;

const ModalMetadata = styled.div`
  background-color: rgba(0, 0, 0, 0.2);
  padding: 1rem;
  margin-top: auto;
  border-left: 3px solid var(--red);
  font-family: monospace;
  font-size: 0.9rem;
  
  p {
    margin-bottom: 0.5rem;
  }
  
  .redacted {
    background-color: #000;
    color: transparent;
    user-select: none;
    padding: 0 10px;
  }
`;

const CloseButton = styled(motion.button)`
  position: absolute;
  top: 1rem;
  right: 1rem;
  background-color: transparent;
  color: var(--white);
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  z-index: 10;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  
  &:before, &:after {
    content: '';
    position: absolute;
    width: 25px;
    height: 2px;
    background-color: var(--white);
    transition: background-color 0.3s;
  }
  
  &:before {
    transform: rotate(45deg);
  }
  
  &:after {
    transform: rotate(-45deg);
  }
  
  &:hover:before, &:hover:after {
    background-color: var(--red);
  }
`;

const SectionTitle = styled(motion.h2)`
  color: var(--white);
  text-transform: uppercase;
  font-size: clamp(2rem, 5vw, 3.5rem);
  margin-bottom: 3rem;
  text-align: center;
  position: relative;
  z-index: 2;
  
  &:after {
    content: '';
    position: absolute;
    bottom: -0.5rem;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 3px;
    background-color: var(--red);
  }
`;

const Archive = () => {
  const controls = useAnimation();
  const [ref, inView] = useInView({
    threshold: 0.2,
    triggerOnce: true
  });
  
  const [selectedFile, setSelectedFile] = useState(null);
  
  useEffect(() => {
    if (inView) {
      controls.start('visible');
    }
  }, [controls, inView]);
  
  // Sample archive data
  const archiveFiles = [
    {
      id: 2,
      title: 'Dimensional Breach',
      type: 'Photo',
      image: 'https://i.ibb.co/PNpQ0W3/dimensional-breach.jpg',
      classified: 'CLASSIFIED',
      mediaType: "image",
      mediaUrl: "https://i.ibb.co/XkVhkVrh/image.png"
    },
    {
      id: 3,
      title: 'Shadow Entities',
      type: 'Video',
      image: 'https://i.ibb.co/yWc07fX/shadow-entities.jpg', // This can be a poster image for the video if you have one
      classified: 'LEVEL 4',
      mediaType: "video",
      mediaUrl: "/video/videoplayback.mp4"
    },
    {
      id: 4,
      title: 'Energy Signatures',
      type: 'Data',
      image: 'https://i.ibb.co/M1NM4g1/energy-signatures.jpg',
      classified: 'RESTRICTED',
      mediaType: "image",
      mediaUrl: "https://i.ibb.co/b5MBV2bt/image.png"
    },
    {
      id: 5,
      title: 'Mantilla Autopsy',
      type: 'Report',
      image: 'https://i.ibb.co/G0zJ7Jb/mantilla-autopsy.jpg',
      classified: 'CONFIDENTIAL',
      mediaType: "image",
      mediaUrl: "https://i.ibb.co/fdh1VsMR/image-2025-06-09-23-16-38.png"
    }
  ];
  
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { 
        staggerChildren: 0.1,
        delayChildren: 0.3
      }
    }
  };
  
  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: { duration: 0.5, ease: 'easeOut' }
    }
  };
  
  const handleFileClick = (file) => {
    setSelectedFile(file);
    // Add body class to prevent scrolling when modal is open
    document.body.style.overflow = 'hidden';
  };
  
  const closeModal = () => {
    setSelectedFile(null);
    // Re-enable scrolling
    document.body.style.overflow = 'auto';
  };
  
  // Generate random text for modal descriptions
  const getRandomFileContent = (file) => {
    let fileDetails = [];
    
    switch(file.type) {
      case 'Document':
        fileDetails = [
          'DATE: [REDACTED]',
          `SUBJECT: ${file.title}`,
          'STATUS: ANOMALOUS',
          'CONTAINMENT: FAILED',
          'CASUALTIES: 7'        
        ];
        break;
      case 'Photo':
        fileDetails = [
          'LOCATION: CERN Section B-14',
          'TIME: 03:42 AM',
          'PHOTOGRAPHER: [REDACTED]',
          'NOTE: Image shows unexplained phenomena during test #31-A'
        ];
        break;
      case 'Video':
        fileDetails = [
          'DURATION: 2:17',
          'RECORDED BY: Security Cam #7',
          'WITNESSES: Dr. Mantilla, J. Weber',
          'STATUS: Under investigation'
        ];
        break;
      default:
        fileDetails = [
          'FILE ID: ' + Math.floor(Math.random() * 9000) + 1000,
          'CLEARANCE: LEVEL 4',
          'RELATED INCIDENT: ADA-31',
          'STATUS: ACTIVE'
        ];
    }
    
    return fileDetails;
  };
  
  return (
    <ArchiveSection ref={ref} id="archive-section">
      <SectionTitle
        initial={{ opacity: 0, y: 30 }}
        animate={controls}
        variants={{
          visible: { 
            opacity: 1, 
            y: 0, 
            transition: { duration: 0.7 } 
          }
        }}
      >
        Classified Archive
      </SectionTitle>
      
      <GridContainer
        variants={containerVariants}
        initial="hidden"
        animate={controls}
      >
        {archiveFiles.map((file) => (
          <FileCard
            key={file.id}
            variants={itemVariants}
            onClick={() => handleFileClick(file)}
            whileHover={{ y: -10 }}
          >
            <FileImage>
              {file.mediaUrl ? (
                <ArchiveMediaContainer>
                  {file.mediaType === 'video' ? (
                    (file.mediaUrl.includes('youtube.com/embed')) ? (
                    <iframe
                      src={`${file.mediaUrl}?controls=0&autoplay=1&modestbranding=1`}
                      title={file.title + ' Media'}
                      frameBorder="0"
                      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                      allowFullScreen
                    ></iframe>
                  ) : (
                    <video controls width="100%" height="100%" style={{ objectFit: 'cover' }} src={process.env.PUBLIC_URL + file.mediaUrl} title={file.title + ' Media'}>
                      Your browser does not support the video tag.
                    </video>
                  )
                  ) : (
                    <img src={file.mediaUrl} alt={file.title + ' Media'} />
                  )}
                </ArchiveMediaContainer>
              ) : (
                <img src={file.image} alt={file.title} />
              )}
            </FileImage>
            <FileInfo>
              <FileName>{file.title}</FileName>
              <FileType>{file.type}</FileType>
            </FileInfo>
            <ClassifiedStamp>{file.classified}</ClassifiedStamp>
          </FileCard>
        ))}
      </GridContainer>
      
      {selectedFile && (
        <ModalOverlay
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          onClick={closeModal}
        >
          <ModalContent
            onClick={(e) => e.stopPropagation()}
            initial={{ scale: 0.9, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            transition={{ duration: 0.3, ease: "easeOut" }}
          >
            <CloseButton 
              onClick={closeModal}
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
            />
            
            <ModalImageContainer>
              {selectedFile.mediaUrl ? (
                <ArchiveMediaContainer>
                  {selectedFile.mediaType === 'video' ? (
                    (selectedFile.mediaUrl.includes('youtube.com/embed')) ? (
                    <iframe
                      src={`${selectedFile.mediaUrl}?controls=0&autoplay=1&modestbranding=1`}
                      title={selectedFile.title + ' Media'}
                      frameBorder="0"
                      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                      allowFullScreen
                    ></iframe>
                  ) : (
                    <video controls width="100%" height="100%" style={{ objectFit: 'cover' }} src={process.env.PUBLIC_URL + selectedFile.mediaUrl} title={selectedFile.title + ' Media'}>
                      Your browser does not support the video tag.
                    </video>
                  )
                  ) : (
                    <img src={selectedFile.mediaUrl} alt={selectedFile.title + ' Media'} />
                  )}
                </ArchiveMediaContainer>
              ) : (
                <img src={selectedFile.image} alt={selectedFile.title} />
              )}
              <ClassifiedStamp>{selectedFile.classified}</ClassifiedStamp>
            </ModalImageContainer>
            
            <ModalInfo>
              <ModalHeader>
                <ModalTitle>{selectedFile.title}</ModalTitle>
                <ModalSubtitle>{selectedFile.type} • File #{Math.floor(Math.random() * 900) + 100}</ModalSubtitle>
              </ModalHeader>
              
              <ModalDescription>
                <p>
                  This {selectedFile.type.toLowerCase()} contains evidence of anomalous activity observed during CERN experiment #{Math.floor(Math.random() * 90) + 10}. 
                </p>
                <p>
                  Multiple verification processes have confirmed the authenticity of this material. 
                  <span className="redacted">Further investigation into dimensional interference patterns has been</span> authorized by oversight committee.
                </p>
              </ModalDescription>
              
              <ModalMetadata>
                {getRandomFileContent(selectedFile).map((detail, index) => (
                  <p key={index}>{detail}</p>
                ))}
                <p>ACCESS: <span className="redacted">AUTHORIZED PERSONNEL ONLY</span></p>
              </ModalMetadata>
            </ModalInfo>
          </ModalContent>
        </ModalOverlay>
      )}
      
    </ArchiveSection>
  );
};

export default Archive;
