import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';
import { motion, useAnimation } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

const FactsSection = styled.section`
  background-color: var(--dark);
  padding: 6rem 1rem;
  position: relative;
  overflow: hidden;
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle, var(--purple-dark) 0%, var(--dark) 70%);
    opacity: 0.5;
    z-index: 0;
  }
`;

const SectionTitle = styled(motion.h2)`
  color: var(--white);
  text-transform: uppercase;
  font-size: clamp(2rem, 5vw, 3.5rem);
  margin-bottom: 3rem;
  text-align: center;
  position: relative;
  z-index: 2;
  
  &:after {
    content: '';
    position: absolute;
    bottom: -0.5rem;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 3px;
    background-color: var(--red);
  }
`;

const SliderContainer = styled(motion.div)`
  width: 100%;
  display: flex; /* Added */
  justify-content: center; /* Added */
  position: relative;
  z-index: 2;
  padding: 0 1rem 2rem;
`;

const SliderContent = styled.div`
  display: flex;
  gap: 1.5rem;
  padding: 1rem 0.5rem;
  overflow-x: auto; /* Added */
  
  /* Custom scrollbar for SliderContent */
  &::-webkit-scrollbar {
    height: 5px;
  }
  
  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
  }
  
  &::-webkit-scrollbar-thumb {
    background: var(--red);
    border-radius: 10px;
  }
`;

const FactCard = styled(motion.div)`
  width: 300px;
  height: 400px;
  flex-shrink: 0;
  background-color: #f5f5dc; /* Vintage paper color */
  color: var(--dark);
  border-radius: 2px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  position: relative;
  transform-style: preserve-3d;
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.1' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)' opacity='0.05'/%3E%3C/svg%3E");
    opacity: 0.2;
    pointer-events: none;
  }
  
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, 
      rgba(245, 245, 220, 0.2) 0%, 
      rgba(245, 245, 220, 0) 20%, 
      rgba(245, 245, 220, 0) 80%, 
      rgba(245, 245, 220, 0.2) 100%);
    pointer-events: none;
  }
  
  &:nth-child(odd) {
    transform: rotate(-1deg);
  }
  
  &:nth-child(even) {
    transform: rotate(1deg);
  }
`;

const FactHeader = styled.div`
  border-bottom: 2px solid rgba(0, 0, 0, 0.15);
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.05);
`;

const FactTitle = styled.h3`
  font-family: 'Times New Roman', serif;
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  color: #111;
`;

const FactDate = styled.div`
  font-size: 0.8rem;
  font-style: italic;
  font-family: 'Times New Roman', serif;
  color: #444;
`;

const FactContent = styled.div`
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
`;

const FactImage = styled.div`
  width: 100%;
  height: 150px;
  overflow: hidden;
  background-color: #ddd;
  position: relative;
  
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.2);
    pointer-events: none;
  }
`;

const FactText = styled.p`
  font-family: 'Times New Roman', serif;
  font-size: 0.95rem;
  line-height: 1.4;
  text-align: justify;
`;

const RedactionMark = styled.span`
  background-color: #000;
  color: transparent;
  user-select: none;
  padding: 0 3px;
`;

const Facts = () => {
  const controls = useAnimation();
  const [ref, inView] = useInView({
    threshold: 0.2,
    triggerOnce: true
  });
  
  const sliderRef = useRef(null);
  
  useEffect(() => {
    if (inView) {
      controls.start('visible');
    }
  }, [controls, inView]);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { 
        duration: 0.8,
        staggerChildren: 0.15
      }
    }
  };
  
  const cardVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.6, ease: "easeOut" }
    },
    hover: {
      y: -10,
      boxShadow: '0 10px 20px rgba(0, 0, 0, 0.5)',
      transition: { duration: 0.3 }
    }
  };
  
  // Sample facts data
  const facts = [
    {
      id: 1,
      title: "CERN Scientist Found Dead",
      date: "June 4, 2025",
      image: "https://i.ibb.co/BHfHgQbg/image-2025-06-09-22-46-08.png", // CERN Scientist Found Dead
      text: "Dr. Alvaro Mantilla was found dead in his apartment after reporting anomalous energy readings. His research data has been completely purged from CERN databases. Officials claim a routine backup error."
    },
    {
      id: 2,
      title: "Unexplained Shadows Recorded",
      date: "May 21, 2025",
      image: "https://i.ibb.co/1fQ1Xpkg/image-2025-06-09-22-47-40.png", // Unexplained Shadows Recorded
      text: "Security footage from CERN's LHC tunnel shows unexplained shadow movements between frames. Witnesses report hearing whispers in an unknown language when playing the recordings backwards."
    },
    {
      id: 3,
      title: "Mass Bird Deaths Near Site",
      date: "April 30, 2025",
      image: "https://i.ibb.co/60ncd6K7/IMG-20250609-224356.png", // Mass Bird Deaths Near Site
      text: "Over 3,000 birds found dead within a 12km radius of CERN facility after a 'routine' test. Autopsy reports indicate unusual cellular structure changes. Document classification: SCP-███"
    },
    {
      id: 4,
      title: "Strange Red Sky Phenomenon",
      date: "March 16, 2025",
      image: "https://i.ibb.co/ZzFsMpXz/image-2025-06-09-22-48-16.png", // Strange Red Sky Phenomenon
      text: "Residents near Geneva reported a blood-red sky directly above the CERN facility lasting approximately 7 minutes. Official explanation: 'unusual atmospheric conditions' despite clear weather reports."
    },
    {
      id: 5,
      title: "Leaked Internal Memo",
      date: "February 2, 2025",
      image: "https://i.ibb.co/TB7ZbtRz/image.png", // Leaked Internal Memo
      text: "Internal document referenced 'breach event ADA-31' and 'dimensional instability'. Subsequent communications ordered staff to evacuate Wing B and initiated Protocol █████. All personnel involved have been reassigned."
    }
  ];

  return (
    <FactsSection ref={ref} id="facts-section">
      <SectionTitle 
        initial={{ opacity: 0, y: 30 }}
        animate={controls}
        variants={{
          visible: { 
            opacity: 1, 
            y: 0, 
            transition: { duration: 0.7 } 
          }
        }}
      >
        Facts They Won't Show On TV
      </SectionTitle>
      
      <SliderContainer 
        ref={sliderRef}
        initial="hidden"
        animate={controls}
        variants={containerVariants}
      >
        <SliderContent>
          {facts.map(fact => (
            <FactCard 
              key={fact.id}
              variants={cardVariants}
              whileHover="hover"
            >
              <FactHeader>
                <FactTitle>{fact.title}</FactTitle>
                <FactDate>{fact.date}</FactDate>
              </FactHeader>
              
              <FactContent>
                <FactImage>
                  <img src={fact.image} alt={fact.title} />
                </FactImage>
                
                <FactText>
                  {fact.text.split(' ').map((word, index) => {
                    // Randomly apply redaction to some words
                    return Math.random() > 0.95 ? 
                      <RedactionMark key={index}>{word}</RedactionMark> : 
                      word + ' '
                  })}
                </FactText>
              </FactContent>
            </FactCard>
          ))}
        </SliderContent>
      </SliderContainer>
    </FactsSection>
  );
};

export default Facts;
