import React, { useEffect } from 'react';
import styled from 'styled-components';
import { motion, useAnimation } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

const FooterSection = styled.section`
  background-color: #0a0a0a;
  padding: 6rem 1rem;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, var(--dark) 0%, #1a0011 50%, var(--dark) 100%);
    opacity: 0.5;
    z-index: 0;
  }
`;

const MainTitle = styled(motion.h2)`
  color: var(--white);
  font-size: clamp(3rem, 10vw, 6rem);
  margin-bottom: 2rem;
  letter-spacing: 5px;
  text-shadow: 0 0 10px rgba(255, 0, 34, 0.5);
  position: relative;
  z-index: 2;
`;

const SubTitle = styled(motion.p)`
  color: var(--gray-light);
  font-size: clamp(1.2rem, 4vw, 2rem);
  margin-bottom: 4rem;
  max-width: 800px;
  position: relative;
  z-index: 2;
`;

const GlitchContainer = styled.div`
  margin-top: 4rem;
  position: relative;
  z-index: 2;
`;

const GlitchMessage = styled(motion.div)`
  font-family: monospace;
  font-size: clamp(0.8rem, 2vw, 1.2rem);
  color: var(--red);
  margin-bottom: 0.5rem;
  opacity: 0.7;
  
  &.flicker {
    animation: flicker 3s infinite;
  }
`;

const ErrorText = styled.div`
  font-family: monospace;
  color: var(--red);
  font-size: clamp(1rem, 3vw, 1.5rem);
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  
  .loading-container {
    display: flex;
    align-items: center;
    margin-top: 1rem;
  }
  
  .loading-dots {
    display: inline-flex;
  }
  
  .dot {
    width: 8px;
    height: 8px;
    margin: 0 5px;
    border-radius: 50%;
    background-color: var(--red);
    animation: pulse 1.5s infinite;
    
    &:nth-child(2) {
      animation-delay: 0.2s;
    }
    
    &:nth-child(3) {
      animation-delay: 0.4s;
    }
  }
  
  @keyframes pulse {
    0%, 100% {
      opacity: 0.2;
      transform: scale(0.8);
    }
    50% {
      opacity: 1;
      transform: scale(1);
    }
  }
`;

const BackToTop = styled(motion.button)`
  position: absolute;
  bottom: 2rem;
  background-color: transparent;
  color: var(--white);
  border: 1px solid var(--red);
  padding: 0.8rem 1.5rem;
  cursor: pointer;
  font-size: 1rem;
  z-index: 10;
  
  &:hover {
    background-color: rgba(255, 0, 34, 0.1);
  }
`;

const NoiseOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)' opacity='0.05'/%3E%3C/svg%3E");
  pointer-events: none;
  z-index: 1;
`;

const Footer = () => {
  const controls = useAnimation();
  const [ref, inView] = useInView({
    threshold: 0.2,
    triggerOnce: true
  });
  
  useEffect(() => {
    if (inView) {
      controls.start('visible');
      
      // Simulate "hang" effect after a few seconds
      const timeout = setTimeout(() => {
        const errorMessages = document.querySelectorAll('.error-message');
        errorMessages.forEach(msg => {
          msg.style.display = 'block';
        });
      }, 5000);
      
      return () => clearTimeout(timeout);
    }
  }, [controls, inView]);
  
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };
  
  const titleVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 1.2, ease: "easeOut" }
    }
  };
  
  const subtitleVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { duration: 1, ease: "easeOut", delay: 0.5 }
    }
  };
  
  const glitchVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { duration: 0.5, staggerChildren: 0.3, delayChildren: 1.5 }
    }
  };
  
  const glitchItemVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { duration: 0.5 }
    }
  };

  return (
    <FooterSection ref={ref} id="footer">
      <MainTitle
        variants={titleVariants}
        initial="hidden"
        animate={controls}
      >
        THE GATE IS ALREADY OPEN
      </MainTitle>
      
      <SubTitle
        variants={subtitleVariants}
        initial="hidden"
        animate={controls}
      >
        NOW THE QUESTION IS NOT WHEN, BUT WHO'S FIRST
      </SubTitle>
      
      <GlitchContainer>
        <motion.div
          variants={glitchVariants}
          initial="hidden"
          animate={controls}
        >
          <GlitchMessage variants={glitchItemVariants} className="flicker">
            CONNECTION UNSTABLE...
          </GlitchMessage>
          
          <GlitchMessage variants={glitchItemVariants}>
            DATA CORRUPTION DETECTED
          </GlitchMessage>
          
          <GlitchMessage variants={glitchItemVariants}>
            ATTEMPTING TO RESTORE CONNECTION...
          </GlitchMessage>
          
          <ErrorText className="error-message" style={{ display: 'none' }}>
            CONNECTION TERMINATED
            <div className="loading-container">
              <span>RECONNECTING</span>
              <div className="loading-dots">
                <div className="dot"></div>
                <div className="dot"></div>
                <div className="dot"></div>
              </div>
            </div>
          </ErrorText>
        </motion.div>
      </GlitchContainer>
      
      <BackToTop
        onClick={scrollToTop}
        whileHover={{ y: -5 }}
        whileTap={{ scale: 0.95 }}
      >
        RETURN TO SURFACE
      </BackToTop>
      
      <NoiseOverlay />
    </FooterSection>
  );
};

export default Footer;
