import React, { useEffect } from 'react';
import styled from 'styled-components';
import { motion, useAnimation } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

const WitnessesSection = styled.section`
  background-color: var(--dark);
  color: var(--white);
  padding: 6rem 1rem;
  position: relative;
  overflow: hidden;
  
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at center, rgba(255, 0, 34, 0.1) 0%, rgba(0, 0, 0, 0) 70%);
    pointer-events: none;
  }
`;

const SectionTitle = styled(motion.h2)`
  color: var(--white);
  text-transform: uppercase;
  font-size: clamp(2rem, 5vw, 3.5rem);
  margin-bottom: 3rem;
  text-align: center;
  position: relative;
  z-index: 2;
  
  &:after {
    content: '';
    position: absolute;
    bottom: -0.5rem;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 3px;
    background-color: var(--red);
  }
`;

const QuotesContainer = styled(motion.div)`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem; 
  max-width: 1100px; 
  margin: 2rem auto;
  padding: 0 1rem; 
`;

const QuoteCard = styled(motion.div)`
  background: rgba(22, 16, 30, 0.75); 
  border: 1px solid var(--neon-purple-transparent);
  padding: 1.5rem 2rem; 
  border-radius: 10px; 
  flex-basis: calc(50% - 0.75rem); 
  box-sizing: border-box;
  box-shadow: 0 0 12px rgba(var(--neon-blue-rgb), 0.25);
  min-width: 280px; 
  margin-bottom: 1.5rem; 
  
  &:nth-child(odd) {
    transform: translateY(10px); 
  }
  
  &:nth-child(even) {
    transform: translateY(-10px); 
  }

  @media (max-width: 768px) {
    flex-basis: calc(100% - 0.75rem); 
    &:nth-child(odd), &:nth-child(even) {
      transform: translateY(0); 
    }
  }
`;

const QuoteContent = styled.blockquote`
  font-size: clamp(1rem, 2.5vw, 1.25rem); 
  font-style: italic;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  position: relative;
  padding-left: 2rem;
  line-height: 1.6;

  &::before {
    content: '“';
    font-family: 'Georgia', serif;
    font-size: 2.8rem; 
    color: var(--red);
    position: absolute;
    left: -0.5rem;
    top: -0.5rem;
    opacity: 0.7;
  }
`;

const QuoteAuthor = styled.p`
  font-size: clamp(0.85rem, 2vw, 0.95rem); 
  color: var(--text-secondary);
  text-align: right;
  
  span {
    font-style: italic;
    color: var(--neon-blue);
  }
`;

const GlitchText = styled.span`
  position: relative;
  display: inline-block;
  
  &:before, &:after {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
  }
  
  &:before {
    animation: glitch-effect 3s infinite;
    color: var(--red);
    clip-path: polygon(0 0, 100% 0, 100% 45%, 0 45%);
    transform: translate(-0.025em, -0.0125em);
    opacity: 0.8;
  }
  
  &:after {
    animation: glitch-effect 2s infinite;
    color: var(--neon-green);
    clip-path: polygon(0 80%, 100% 20%, 100% 100%, 0 100%);
    transform: translate(0.025em, 0.0125em);
    opacity: 0.8;
  }
  
  @keyframes glitch-effect {
    0% { 
      opacity: 0;
      transform: translate(0);
    }
    20% { 
      opacity: 0.8;
    }
    21.999% { 
      opacity: 0.8;
    }
    22% { 
      opacity: 0;
    }
    92% { 
      opacity: 0;
    }
    92.999% { 
      opacity: 0.8;
    }
    93% { 
      opacity: 0;
    }
    99.999% { 
      opacity: 0.8;
    }
    100% { 
      opacity: 0;
      transform: translate(0);
    }
  }
`;

const witnessesData = [
  {
    id: 1,
    quote: "We heard screams coming from beneath the ground... They weren't human.",
    author: "Anonymous Technician",
    position: "CERN Maintenance Staff"
  },
  {
    id: 2,
    quote: "The 'accident' was no accident. They opened something they shouldn't have.",
    author: "Dr. Aris Thorne (Fictional)",
    position: "Former CERN Physicist"
  },
  {
    id: 3,
    quote: "Sometimes, late at night, the entire facility hums... a deep, resonant frequency that unsettles the soul.",
    author: "Security Officer Miller",
    position: "Night Shift Supervisor"
  },
  {
    id: 4,
    quote: "They say it's for science. But what I saw... it was something else. Something ancient.",
    author: "Elara Vance (Fictional)",
    position: "Visiting Researcher"
  }
];

const cardItemVariants = {
  hidden: { opacity: 0, y: 25 },
  visible: (i) => ({
    opacity: 1,
    y: 0,
    transition: {
      delay: i * 0.18, 
      duration: 0.55,
      ease: "easeOut",
    },
  }),
};

const Witnesses = () => {
  const sectionControls = useAnimation();
  const [sectionRef, sectionInView] = useInView({
    threshold: 0.15, 
    triggerOnce: true,
  });

  useEffect(() => {
    if (sectionInView) {
      sectionControls.start('visible');
    }
  }, [sectionControls, sectionInView]);

  const sectionContainerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        duration: 0.4,
      },
    },
  };

  return (
    <WitnessesSection
      ref={sectionRef}
      id="witnesses-section"
      variants={sectionContainerVariants}
      initial="hidden"
      animate={sectionControls}
    >
      <SectionTitle
        custom={0} 
        variants={cardItemVariants} 
      >
        What The <GlitchText data-text="Witnesses">Witnesses</GlitchText> Say
      </SectionTitle>
      
      <QuotesContainer> 
        {witnessesData.map((witness, index) => (
          <QuoteCard
            key={witness.id}
            custom={index} 
            variants={cardItemVariants}
          >
            <QuoteContent>
              <GlitchText data-text={witness.quote}>{witness.quote}</GlitchText>
            </QuoteContent>
            <QuoteAuthor>
              {witness.author} — <span>{witness.position}</span>
            </QuoteAuthor>
          </QuoteCard>
        ))}
      </QuotesContainer>
    </WitnessesSection>
  );
};

export default Witnesses;
