import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { motion, useAnimation } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

const AIContainer = styled.section`
  background-color: #0a0a0a;
  color: var(--white);
  padding: 6rem 1rem;
  position: relative;
  overflow: hidden;
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(ellipse at bottom, rgba(0, 255, 204, 0.05) 0%, transparent 70%);
    pointer-events: none;
  }
`;

const ContentWrapper = styled.div`
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
`;

const SectionTitle = styled(motion.h2)`
  color: var(--white);
  text-transform: uppercase;
  font-size: clamp(2rem, 5vw, 3.5rem);
  margin-bottom: 1rem;
  text-align: center;
  position: relative;
  z-index: 2;
  
  &:after {
    content: '';
    position: absolute;
    bottom: -0.5rem;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 3px;
    background-color: var(--neon-green);
  }
`;

const Subtitle = styled(motion.p)`
  text-align: center;
  font-size: clamp(1rem, 3vw, 1.2rem);
  margin-bottom: 3rem;
  color: var(--gray-light);
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
`;

const FormContainer = styled(motion.div)`
  background-color: rgba(10, 10, 10, 0.8);
  border: 1px solid rgba(0, 255, 204, 0.3);
  border-radius: 4px;
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: 0 0 20px rgba(0, 255, 204, 0.1);
  position: relative;
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, rgba(0, 255, 204, 0.02) 0%, rgba(0, 0, 0, 0) 70%);
    pointer-events: none;
  }
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
`;

const FormLabel = styled.label`
  color: var(--white);
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
  display: block;
`;

const DateInput = styled.input`
  background-color: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 0.8rem 1rem;
  margin-bottom: 1.5rem;
  color: var(--white);
  font-size: 1rem;
  border-radius: 3px;
  
  &:focus {
    outline: none;
    border-color: var(--neon-green);
    box-shadow: 0 0 10px rgba(0, 255, 204, 0.2);
  }
  
  &::placeholder {
    color: rgba(255, 255, 255, 0.3);
  }
`;

const SubmitButton = styled(motion.button)`
  background-color: var(--neon-green);
  color: #000;
  padding: 0.8rem 1.5rem;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 700;
  text-transform: uppercase;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  align-self: center;
  
  &:before {
    content: '';
    position: absolute;
    top: -50%;
    left: -60%;
    width: 200%;
    height: 200%;
    background: rgba(255, 255, 255, 0.1);
    transform: rotate(45deg);
    transition: all 0.5s ease;
  }
  
  &:hover:before {
    left: 100%;
  }
  
  &:hover {
    background-color: var(--white);
    box-shadow: 0 0 15px rgba(0, 255, 204, 0.5);
  }
`;

const ResponseContainer = styled(motion.div)`
  margin-top: 2rem;
  text-align: center;
  font-size: clamp(1.2rem, 4vw, 2rem);
  position: relative;
`;

const AccessMessage = styled(motion.div)`
  margin-bottom: 1.5rem;
  font-weight: 700;
  color: var(--neon-green);
  text-transform: uppercase;
`;

const PercentageDisplay = styled(motion.div)`
  font-size: clamp(2rem, 6vw, 4rem);
  font-weight: 700;
  margin: 1.5rem 0;
  letter-spacing: 3px;
`;

const FinalMessage = styled.p`
  font-style: italic;
  margin-top: 2rem;
  text-align: center;
  color: var(--white);
  font-size: 1.2rem;
  opacity: 0.8;
`;

const StatusBox = styled(motion.div)`
  background-color: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255,0,34,0.3);
  padding: 0.5rem;
  margin-bottom: 1rem;
  color: var(--red);
  font-family: monospace;
  text-align: left;
`;

const AISection = () => {
  const [birthdate, setBirthdate] = useState('');
  const [submitted, setSubmitted] = useState(false);
  const [percentage, setPercentage] = useState(0);
  const [showPercentage, setShowPercentage] = useState(false);
  const [loadingStatus, setLoadingStatus] = useState('');
  
  const controls = useAnimation();
  const [ref, inView] = useInView({
    threshold: 0.3,
    triggerOnce: true
  });
  
  useEffect(() => {
    if (inView) {
      controls.start('visible');
    }
  }, [controls, inView]);
  
  useEffect(() => {
    if (submitted) {
      // Simulate terminal-like loading messages
      const statusMessages = [
        'Initializing neural connection...',
        'Accessing ADA database...',
        'Analyzing quantum resonance patterns...',
        'Calculating dimensional compatibility...',
        'Processing chronological alignment...',
        'Connection established. Transferring data...'
      ];
      
      let currentIndex = 0;
      
      const intervalId = setInterval(() => {
        if (currentIndex < statusMessages.length) {
          setLoadingStatus(prevStatus => prevStatus + '\n' + statusMessages[currentIndex]);
          currentIndex++;
        } else {
          clearInterval(intervalId);
          
          // Show percentage after status messages complete
          setShowPercentage(true);
          
          // Animate percentage counter
          let count = 0;
          const finalPercentage = Math.floor(Math.random() * 31) + 70; // Random between 70-100
          
          const percentIntervalId = setInterval(() => {
            if (count < finalPercentage) {
              count += 1;
              setPercentage(count);
            } else {
              clearInterval(percentIntervalId);
            }
          }, 30);
        }
      }, 1000);
      
      return () => {
        clearInterval(intervalId);
      };
    }
  }, [submitted]);
  
  const handleSubmit = (e) => {
    e.preventDefault();
    setSubmitted(true);
  };
  
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { 
        duration: 0.8,
        staggerChildren: 0.3
      }
    }
  };
  
  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: { duration: 0.6, ease: "easeOut" }
    }
  };

  return (
    <AIContainer ref={ref} id="ai-section">
      <ContentWrapper>
        <SectionTitle
          initial={{ opacity: 0, y: 30 }}
          animate={controls}
          variants={{
            visible: { 
              opacity: 1, 
              y: 0, 
              transition: { duration: 0.7 } 
            }
          }}
        >
          ADA Interface
        </SectionTitle>
        
        <Subtitle
          initial={{ opacity: 0 }}
          animate={controls}
          variants={{
            visible: { 
              opacity: 1,
              transition: { duration: 0.7, delay: 0.3 } 
            }
          }}
        >
          Enter your birthdate to calculate your dimensional resonance with the portal
        </Subtitle>
        
        {!submitted ? (
          <FormContainer
            variants={containerVariants}
            initial="hidden"
            animate={controls}
          >
            <Form onSubmit={handleSubmit}>
              <FormLabel>Enter your birthdate:</FormLabel>
              <DateInput
                type="text"
                placeholder="DD.MM.YYYY"
                value={birthdate}
                onFocus={(e) => (e.target.type = 'date')}
                onBlur={(e) => { if (!e.target.value) e.target.type = 'text'; }}
                onChange={(e) => setBirthdate(e.target.value)}
                required
              />
              
              <SubmitButton
                variants={itemVariants}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                type="submit"
              >
                Calculate Compatibility
              </SubmitButton>
            </Form>
          </FormContainer>
        ) : (
          <ResponseContainer
            initial={{ opacity: 0 }}
            animate={{ opacity: 1, transition: { duration: 0.5 } }}
          >
            {loadingStatus && (
              <StatusBox
                initial={{ height: 0, opacity: 0 }}
                animate={{ 
                  height: 'auto', 
                  opacity: 1, 
                  transition: { duration: 0.3 } 
                }}
              >
                <pre>{loadingStatus}</pre>
              </StatusBox>
            )}
            
            {showPercentage && (
              <>
                <AccessMessage
                  initial={{ opacity: 0 }}
                  animate={{ 
                    opacity: 1, 
                    transition: { duration: 0.5, delay: 0.5 } 
                  }}
                >
                  Access Granted. You have been chosen.
                </AccessMessage>
                
                <PercentageDisplay
                  initial={{ scale: 0.8, opacity: 0 }}
                  animate={{ 
                    scale: 1, 
                    opacity: 1, 
                    transition: { duration: 0.5, delay: 0.7 } 
                  }}
                >
                  {percentage}% compatibility with ADA-portal
                </PercentageDisplay>
              </>
            )}
          </ResponseContainer>
        )}
        
        <FinalMessage>
          "You are either the last hope or the first horseman."
        </FinalMessage>
      </ContentWrapper>
    </AIContainer>
  );
};

export default AISection;
