import React from 'react';
import styled from 'styled-components';
import { motion, useAnimation } from 'framer-motion';
import { useInView } from 'react-intersection-observer';
import { useEffect } from 'react';

const AnomaliesSection = styled.section`
  background-color: var(--dark);
  position: relative;
  overflow: hidden;
  padding: 6rem 1rem;
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, var(--dark), #1a0011, var(--dark));
    opacity: 0.8;
  }
`;

const SectionTitle = styled(motion.h2)`
  color: var(--white);
  text-transform: uppercase;
  font-size: clamp(2rem, 5vw, 3.5rem);
  margin-bottom: 3rem;
  text-align: center;
  position: relative;
  z-index: 2;
  
  &:after {
    content: '';
    position: absolute;
    bottom: -0.5rem;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 3px;
    background-color: var(--red);
  }
`;

const GridContainer = styled(motion.div)`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
  padding: 1rem;
  
  @media (min-width: 768px) {
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  }
`;

const AnomalyCard = styled(motion.div)`
  background-color: rgba(15, 15, 15, 0.9);
  border: 1px solid rgba(255, 0, 34, 0.2);
  border-radius: 4px;
  padding: 1.5rem;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  height: 100%;
  min-height: 250px;
  display: flex;
  flex-direction: column;
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, rgba(255,0,34,0.05) 0%, rgba(0,0,0,0) 70%);
    pointer-events: none;
  }
  
  .anomaly-icon {
    width: 60px;
    height: 60px;
    margin-bottom: 1.5rem;
    position: relative;
    
    svg {
      width: 100%;
      height: 100%;
      fill: var(--red);
    }
  }
`;

const AnomalyTitle = styled.h3`
  color: var(--white);
  font-size: 1.5rem;
  margin-bottom: 1rem;
  position: relative;
  padding-bottom: 0.75rem;
  
  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 50px;
    height: 2px;
    background-color: var(--red);
  }
`;

const AnomalyDescription = styled.p`
  color: var(--gray-light);
  font-size: 1rem;
  line-height: 1.5;
  margin-top: auto;
  position: relative;
`;

const CodeOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, 
    rgba(0, 255, 204, 0.03) 25%, 
    transparent 25%, 
    transparent 50%, 
    rgba(0, 255, 204, 0.03) 50%, 
    rgba(0, 255, 204, 0.03) 75%, 
    transparent 75%, 
    transparent);
  background-size: 4px 4px;
  opacity: 0.3;
  pointer-events: none;
`;

const StarIcon = () => (
  <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
    <path d="M12 0L14.645 8.35H23.4171L16.386 13.45L19.031 21.8L12 16.7L4.969 21.8L7.614 13.45L0.583 8.35H9.355L12 0Z"/>
  </svg>
);

const BirdIcon = () => (
  <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
    <path d="M22,3c-1.5,0-2.9,0.4-4.1,1.1C16.4,2.8,14.3,2,12,2C9.7,2,7.6,2.8,6.1,4.1C4.9,3.4,3.5,3,2,3v4c1.6,0,3.1,0.6,4.3,1.7 C5.5,9.9,5,11.4,5,13v6h2v-6c0-1.3,0.4-2.5,1-3.5C9,8.6,10.4,8,12,8c1.6,0,3,0.6,4,1.5c0.6,1,1,2.2,1,3.5v6h2v-6 c0-1.6-0.5-3.1-1.3-4.3C18.9,7.6,20.4,7,22,7V3z"/>
  </svg>
);

const SoundIcon = () => (
  <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
    <path d="M3,9v6h4l5,5V4L7,9H3z M16.5,12c0-1.77-1.02-3.29-2.5-4.03v8.05C15.48,15.29,16.5,13.77,16.5,12z M14,3.23v2.06 c2.89,0.86,5,3.54,5,6.71s-2.11,5.85-5,6.71v2.06c4.01-0.91,7-4.49,7-8.77S18.01,4.14,14,3.23z"/>
  </svg>
);

const DnaIcon = () => (
  <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
    <path d="M4,2h2v12h12V2h2v20h-2V16H6v6H4V2z M18,12V8H6v4H18z M18,6V4H6v2H18z M6,14h12v-2H6V14z M6,18h12v-2H6V18z"/>
  </svg>
);

const EyeIcon = () => (
  <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
    <path d="M12,4.5C7,4.5,2.73,7.61,1,12c1.73,4.39,6,7.5,11,7.5s9.27-3.11,11-7.5C21.27,7.61,17,4.5,12,4.5z M12,17 c-2.76,0-5-2.24-5-5s2.24-5,5-5s5,2.24,5,5S14.76,17,12,17z M12,9c-1.66,0-3,1.34-3,3s1.34,3,3,3s3-1.34,3-3S13.66,9,12,9z"/>
  </svg>
);

const TimeIcon = () => (
  <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
    <path d="M11.99,2C6.47,2,2,6.48,2,12s4.47,10,9.99,10C17.52,22,22,17.52,22,12S17.52,2,11.99,2z M12,20c-4.42,0-8-3.58-8-8 s3.58-8,8-8s8,3.58,8,8S16.42,20,12,20z M12.5,7H11v6l5.25,3.15l0.75-1.23l-4.5-2.67V7z"/>
  </svg>
);

const Anomalies = () => {
  const controls = useAnimation();
  const [ref, inView] = useInView({
    threshold: 0.2,
    triggerOnce: true
  });

  useEffect(() => {
    if (inView) {
      controls.start('visible');
    }
  }, [controls, inView]);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { 
        staggerChildren: 0.15,
        delayChildren: 0.3
      }
    }
  };
  
  const cardVariants = {
    hidden: { y: 50, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: { duration: 0.5, ease: "easeOut" }
    },
    hover: {
      y: -10,
      boxShadow: '0 10px 20px rgba(255, 0, 34, 0.2), 0 0 15px rgba(0, 255, 204, 0.2)',
      borderColor: 'rgba(255, 0, 34, 0.5)',
      transition: { duration: 0.3 }
    }
  };

  const anomalies = [
    {
      id: 1,
      title: "Stars Disappearing",
      description: "Astronomers report that stars are no longer visible directly above the CERN facility in a perfect 1.3km radius circle.",
      icon: <StarIcon />
    },
    {
      id: 2,
      title: "Mass Bird Deaths",
      description: "Over 5,000 birds have been found dead in the surrounding area, with no signs of trauma or disease.",
      icon: <BirdIcon />
    },
    {
      id: 3,
      title: "Auditory Anomalies",
      description: "Strange sounds recorded in the sky during collider activation. Sound analysis shows frequencies not naturally occurring on Earth.",
      icon: <SoundIcon />
    },
    {
      id: 4,
      title: "DNA Alterations",
      description: "Local researchers found altered DNA in plant specimens collected within 500m of the facility. Changes match no known pattern.",
      icon: <DnaIcon />
    },
    {
      id: 5,
      title: "Temporal Distortions",
      description: "Multiple reports of time anomalies, including watches stopping or running backward within the facility perimeter.",
      icon: <TimeIcon />
    },
    {
      id: 6,
      title: "Visual Hallucinations",
      description: "87% of personnel report seeing shadowy figures in peripheral vision after spending more than 4 hours in the lower levels.",
      icon: <EyeIcon />
    }
  ];

  return (
    <AnomaliesSection ref={ref} id="anomalies-section">
      <SectionTitle
        initial={{ opacity: 0, y: 30 }}
        animate={controls}
        variants={{
          visible: { 
            opacity: 1, 
            y: 0, 
            transition: { duration: 0.7 } 
          }
        }}
      >
        Recorded Anomalies
      </SectionTitle>
      
      <GridContainer
        variants={containerVariants}
        initial="hidden"
        animate={controls}
      >
        {anomalies.map((anomaly) => (
          <AnomalyCard
            key={anomaly.id}
            variants={cardVariants}
            whileHover="hover"
          >
            <div className="anomaly-icon">
              {anomaly.icon}
            </div>
            <AnomalyTitle>{anomaly.title}</AnomalyTitle>
            <AnomalyDescription>{anomaly.description}</AnomalyDescription>
            <CodeOverlay />
          </AnomalyCard>
        ))}
      </GridContainer>
    </AnomaliesSection>
  );
};

export default Anomalies;
