import React, { useEffect } from 'react';
import styled from 'styled-components';
import { motion, useAnimation } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

// Placeholder for portal fire animation
const portalGif = "https://media2.giphy.com/media/v1.Y2lkPTc5MGI3NjExNW9weTJidGUzb2I3MWNqYm40bDI4NjgyZm1hdjExOHd3bW9jZXJ3biZlcD12MV9pbnRlcm5hbF9naWZfYnlfaWQmY3Q9Zw/3o6ozFNmtVPIWbJQf6/giphy.gif";

const PortalSection = styled.section`
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  padding: 0;
  background-color: var(--dark);
`;

const BackgroundVideo = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
  opacity: 0.6;
  
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 90%);
  }
  
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
`;

const ContentWrapper = styled.div`
  position: relative;
  z-index: 2;
  text-align: center;
  padding: 1rem;
  max-width: 1000px;
`;

const Title = styled(motion.h1)`
  font-size: clamp(3rem, 10vw, 7rem);
  color: var(--white);
  text-transform: uppercase;
  margin-bottom: 1rem;
  letter-spacing: 3px;
  line-height: 0.9;
  
  .highlight {
    color: var(--red);
    display: block;
    font-size: clamp(4rem, 12vw, 9rem);
    position: relative;
  }
`;

const Subtitle = styled(motion.p)`
  font-size: clamp(1rem, 3vw, 1.5rem);
  color: var(--gray-light);
  max-width: 600px;
  margin: 0 auto 3rem;
  font-weight: 300;
`;

const ExploreButton = styled(motion.button)`
  background-color: var(--red);
  padding: 1rem 2.5rem;
  font-size: 1.2rem;
  position: relative;
  overflow: hidden;
  
  &:before {
    content: '';
    position: absolute;
    top: -50%;
    left: -60%;
    width: 200%;
    height: 200%;
    background: rgba(255, 255, 255, 0.1);
    transform: rotate(45deg);
    transition: all 0.5s ease;
  }
  
  &:hover:before {
    left: 100%;
  }
`;

const NoiseOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)' opacity='0.05'/%3E%3C/svg%3E");
  pointer-events: none;
  z-index: 3;
  opacity: 0.05;
`;

const Portal = () => {
  const controls = useAnimation();
  const [ref, inView] = useInView({
    threshold: 0.3,
    triggerOnce: true
  });

  useEffect(() => {
    if (inView) {
      controls.start('visible');
    }
  }, [controls, inView]);

  const titleVariants = {
    hidden: { y: 50, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: { duration: 0.8, ease: "easeOut" }
    }
  };

  const subtitleVariants = {
    hidden: { y: 30, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: { duration: 0.6, delay: 0.4, ease: "easeOut" }
    }
  };

  const buttonVariants = {
    hidden: { y: 30, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: { duration: 0.5, delay: 0.8, ease: "easeOut" }
    },
    hover: {
      scale: 1.05,
      boxShadow: "0 0 15px var(--red)",
      transition: { duration: 0.3, ease: "easeInOut" }
    },
    tap: { scale: 0.95 }
  };
  
  const scrollToNextSection = () => {
    const factsSection = document.getElementById('facts-section');
    factsSection?.scrollIntoView({ behavior: 'smooth' });
  };

  return (
    <PortalSection ref={ref}>
      <BackgroundVideo>
        <img src={portalGif} alt="Portal fire animation" />
      </BackgroundVideo>
      
      <ContentWrapper>
        <Title
          variants={titleVariants}
          initial="hidden"
          animate={controls}
          data-text="CERN GATE"
          className="glitch-effect"
        >
          CERN
          <span className="highlight flicker">GATE</span>
          ▸ PORTAL IS ALREADY OPEN
        </Title>
        
        <Subtitle
          variants={subtitleVariants}
          initial="hidden"
          animate={controls}
        >
          "And this is not just a scientific error. It has already begun."
        </Subtitle>
        
        <ExploreButton
          variants={buttonVariants}
          initial="hidden"
          animate={controls}
          whileHover="hover"
          whileTap="tap"
          onClick={scrollToNextSection}
        >
          VIEW EVIDENCE
        </ExploreButton>
      </ContentWrapper>
      
      <NoiseOverlay />
    </PortalSection>
  );
};

export default Portal;
